
import robonomics from "../robonomics";
import config from "../config";

export default {
  data() {
    return {
      text: "",
      isSubscription: false,
      discord: config.discord
    };
  },
  created() {
    setInterval(() => {
      this.isSubscription = robonomics.accountManager.subscription;
    }, 1000);
  },
  methods: {
    submit() {
      this.$emit("submit", this.text);
    }
  }
};
